var iconList = JSON.parse(icons);
var iconData = "";

class RemixIcons extends Component {
  constructor(props) {
    super(props);
    this.state = {
      breadcrumbItems: [
        { title: "Icons", link: "#" },
        { title: "Remix Icons", link: "#" },
      ],
    }
  }

  componentDidMount() {
    Object.entries(iconList).map((key, value) => {
      if (key[0] === "Editor") {
        iconData =
          iconData +
          '<div className="card"><div class="card-body"><h4 class="card-title">' +
          key[0] +
          ' </h4><p class="card-title-desc mb-2">Use <code>&lt;i class="ri-bold"&gt;&lt;/i&gt;</code> <span class="badge badge-success">v 2.4.1</span>.</p><div class="row icon-demo-content">';
        for (let k in key[1]) {
          iconData +=
            '<div class="col-xl-3 col-lg-4 col-sm-6"><i class="ri-' +
            k +
            '"></i> ri-' +
            k +
            "</div>";
        }
      } else {
        iconData =
          iconData +
          '<div class="card"><div class="card-body"><h4 class="card-title">' +
          key[0] +
          ' </h4><p class="card-title-desc mb-2">Use <code>&lt;i class="ri-home-line"&gt;&lt;/i&gt;</code> or <code>&lt;i class="ri-home-fill"&gt;&lt;/i&gt;</code> <span class="badge badge-success">v 2.4.1</span>.</p><div class="row icon-demo-content">';
        for (let k in key[1]) {
          iconData += '<div class="col-xl-3 col-lg-4 col-sm-6"><i class="ri-' +
            k +
            '-line"></i> ri-' +
            k +
            '-line</div><div class="col-xl-3 col-lg-4 col-sm-6"><i class="ri-' +
            k +
            '-fill"></i> ri-' +
            k +
            "-fill </div>";
        }
      }
      iconData += "</div></div></div>";
      return 0;
    }

    );
    document.getElementById("icons").innerHTML = iconData;
  }

  render() {
    return (
      <React.Fragment>
        <div className="page-content">
          <Container fluid>

            <Breadcrumbs title="Remix Icons" breadcrumbItems={this.state.breadcrumbItems} />

            <Row>
              <Col xs={12} id="icons">

              </Col>
            </Row>

          </Container>
        </div>
      </React.Fragment>
    );
  }
}

export default RemixIcons;